
#vehicle {
    /* width: 100%; */
    /* padding: 2rem; */
    background-color: #ffffff;
    /* border-radius: 8px; */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
    box-sizing: border-box;
    overflow: auto;
    position: absolute;
    top: 9%;
    left: 5%;
    width: 95%;
    height: 91%;
    overflow-y: auto;
    box-sizing: border-box;
}

h1 {
    align-self: center;
    color: black;
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    margin-left: 50px;
    margin-top: 20px;
}

/* Form Section */
#vehicle .form-section {
    display: -moz-groupbox;
    /* flex-wrap: wrap; */
    flex-direction: column;
    background-color: #ffffff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 10px;
    margin: 30px;
    /* gap: 20px; */
    width: auto;
    max-width: 95%;
}

.form-left {
    display: flex;
    /* flex-wrap: wrap; */
    flex-direction: column;
    width: 100%;
    gap: 10px;
    align-self: start;
}

.form-right {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    width: 75%;
    margin-top: 20px;
    justify-content: space-between;
    /* gap: 20px; */
}

#vehicle .submit-button {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    background-color: #008CBA;
    color: white;
    font-size: 18px;
    font-weight: bold;
    transition: background-color 0.3s ease;
    margin: 30px;
    align-self: self-start;
}

#vehicle .submit-button:hover {
    background-color: #45a049;
}

#submitMessage {
    margin-top: 10px;
    color: green;
    text-align: center;
    font-size: 14px;
}

#vehicle label {
    font-size: 16px;
    font-weight: bold;
    color: #333;
    width: auto;
    align-self: self-start;
    margin-left: 30px;
}

#vehicle input[type="text"],
#vehicle input[type="number"] {
    width: 30%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    transition: border-color 0.3s ease;
    margin-left: 30px;
}

#vehicle input[type="text"]:focus,
#vehicle input[type="number"]:focus,
#vehicle input[type="file"]:focus,
#vehicle input[type="date"]:focus {
    border: 2px solid green;
    outline: none;
}

#vehicle .form-section input[type="date"],
#vehicle .form-section input[type="file"] {
    padding: 5px;
    font-size: 12px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    transition: border-color 0.3s ease;
    align-items: start;
    margin-left: 7px;
}

.field-table {
    width: 100%;
    border-collapse: collapse;
    justify-content: start;
    margin-left: 30px;
    /* background-color: #ffffff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 20px; */
}

.field-table th, .field-table td {
    padding: 10px;
    font-size: 12px;
    border: 1px solid #ddd;
    text-align: left;
}

.field-table th {
    background-color: #007bff;
    color: white;
    text-transform: uppercase;
}

.field-table tr:nth-child(even) {
    background-color: #f9f9f9;
}

.field-table tr:hover {
    background-color: #f1f1f1;
}

.tiles-view {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-self: center;
    gap: 20px;
    padding: 20px;
    background-color: #ffffff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    width: 95%;
    margin: 20px;
}

.tile {
    /* max-width: 500px; */
    width: 45%;
    padding: 15px;
    margin: 15px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.tile-header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 10px;
    font-size: 16px;
    font-weight: bold;
}

.tile-header h2,
.tile-header h5,
.tile-header h4 {
    margin: 0;
}

.tile-table {
    width: 100%;
    border-collapse: collapse;
    margin: 10px 0;
}

.tile-row {
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    padding: 10px;
    font-weight: bold;
    background-color: #eaeaea;
}

.tile-row.tile-header-row {
    background-color: #d8d8d8;
}

.tile-cell {
    padding: 8px;
    text-align: center;
    font-size: 14px;
    border-bottom: 1px solid #ddd;
}

.tile-cell.highlighted {
    color: red;
    font-weight: bold;
}

/* Download Button */
.download-button {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    padding: 8px 6px;
    background-color: #007bff;
    color: white;
    border-radius: 20px;
    /* font-size: 14px; */
    transition: background-color 0.3s ease;
}

.download-button:hover {
    background-color: green;
}

.upload-button {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    padding: 8px 6px;
    background-color: red;
    color: white;
    border-radius: 20px;
    /* font-size: 14px; */
    transition: background-color 0.3s ease;
}




.upload-button:hover {
    background-color: #218838;
}

.vehicle_icons {
    margin-right: 5px;
    font-size: 16px;
}



/* Media Queries */
@media (max-width: 768px) {
    .form-section {
        gap: 10px;
    }

    .tiles-view {
        padding: 10px;
    }

    .tile {
        max-width: 100%;
    }

    .submit-button {
        width: 100%;
    }

    .field-table th, .field-table td {
        font-size: 12px;
        padding: 8px;
    }
}



@media (max-width: 992px) {
    #vehicle .form-section,
    #vehicle .tiles-view {
        width: 98%;
    }

    #vehicle .tile {
        width: calc(100% - 40px);
    }

    #vehicle .field-table td {
        padding: 10px;
    }

    #vehicle .form-section input[type="date"],
    #vehicle .form-section input[type="file"] {
        font-size: 13px;
    }
}

@media (max-width: 768px) {
    #vehicle {
        left: 9%;
        width: 91%;
        padding: 10px;
    }

    #vehicle .form-section,
    #vehicle .tiles-view {
        width: 98%;
        padding: 15px;
        margin: 10px auto;
    }

    #vehicle .field-table td {
        padding: 8px;
    }

    #vehicle .field-table label {
        font-size: 12px;
    }

    #vehicle .form-section input[type="date"],
    #vehicle .form-section input[type="file"] {
        font-size: 12px;
        padding: 6px;
    }

    #vehicle .tile {
        width: 100%;
        margin: 10px 0;
    }

    #vehicle .tile-header {
        flex-direction: column;
        align-items: flex-start;
        gap: 5px;
    }

    #vehicle input[type="text"],
    #vehicle input[type="number"] {
        max-width: 100%;
    }

    #vehicle .submit-button {
        width: 100%;
        margin: 20px auto;
    }
}
